// Google Fonts
@import url('https://fonts.googleapis.com/css?family=Open+Sans:300,400,600,700');
@import url('https://fonts.googleapis.com/css?family=Montserrat:400,700');


// Font Variables 
$open-sans: 'Open Sans';
$montserrat: 'Montserrat';

// Other Variables
$colorBlack: #000000;
$colorWhite: #ffffff;

// Styles
html{
	background-color: #f1f1f1;
	padding: 0;
	margin: 0;
}

h1{
	font-family: $montserrat;
	font-size: 2em !important;
	color: #4C4C4C;	
	font-weight: 700;
}

h2{
	font-family: $montserrat;
	text-transform: uppercase;
	color: #4C4C4C;
	font-size: 33px;
	font-weight: 700;
}

h4{
	font-family: $montserrat;
	text-transform: uppercase;
	font-size: 14px;
	color: #4C4C4C;
	font-weight: 400;
}

h5{
	font-family: $montserrat;
	color: #4C4C4C;
	font-weight: 400;
}

p{
	font-family: $open-sans;
	font-weight: 300;
	color: $colorBlack;
}

.uk-text{
    font-size: 16px;
}

// Navigation
.navigation{
	z-index: 1000;
}

.uk-navbar{
	background: $colorWhite;
	border: 0px;
	height: 51px;
	padding-top:18px;
	box-shadow: 0 0 3px 0 rgba(0, 0, 0, 0.22);
    -moz-box-shadow: 0 0 3px 0 rgba(0, 0, 0, 0.22);
    -webkit-box-shadow: 0 0 3px 0 rgba(0, 0, 0, 0.22);
}

.uk-navbar-nav>li>a {
	font-family: $open-sans;
	font-weight: 300;
	font-size: 15px;
	display: inline-block;
	text-decoration: none;
}

.uk-navbar-nav>li>a:hover {
	
}

.uk-navbar-nav>li.uk-open>a, .uk-navbar-nav>li:hover>a, .uk-navbar-nav>li>a:focus {
    background-color: transparent;
    border-left-color: rgba(0,0,0,.0);
    border-right-color: rgba(0,0,0,.0);
    border-top-color: rgba(0,0,0,.0);
    box-shadow: inset 0 2px 4px rgba(0,0,0,.0);
}

.uk-navbar-center{
	max-width:none;
}

// Offcanvas

.uk-offcanvas-bar{
	background: $colorWhite;
}

.uk-nav-offcanvas>li>a {
    color: $colorBlack;
    padding: 10px 15px;
    border-top: 1px solid rgba(0,0,0,.3);
    text-shadow: none;
    font-family: $open-sans;
}

// Container
.uk-container-center{
	max-width: 1100px;
	padding: 50px;
}

// Slides
.slideAmsterdam{
	background-image: url("../images/scene-1(a).png");
    background-size:contain;
	background-repeat: no-repeat;
    animation: background-fade 2s forwards;
    -webkit-animation: background-fade 2s forwards;
    -moz-animation: background-fade 2s forwards;
    animation-delay: 2.5s;
}
.uk-animation-slide-right > img{
    max-width: 80% !important;
}
@-webkit-keyframes background-fade {
    99.9% {
        background: url("../images/scene-1(c).png");
        background-repeat: no-repeat;
        background-size:contain;
    }
    100% {
        background: url("../images/scene-1(c).png");
        background-repeat: no-repeat;
        background-size:contain;
    }
}
@-moz-keyframes background-fade {
    99.9% {
        background: url("../images/scene-1(c).png");
        background-repeat: no-repeat;
        background-size:contain;
    }
    100% {
        background: url("../images/scene-1(c).png");
        background-repeat: no-repeat;
        background-size:contain;
    }
}
@keyframes background-fade {
    99.9% {
        background: url("../images/scene-1(c).png");
        background-repeat: no-repeat;
        background-size:contain;
    }
    100% {
        background: url("../images/scene-1(c).png");
        background-repeat: no-repeat;
        background-size:contain;
    }
}

.vliegtuig{
	background-image: url("../images/scene-1(b).png");
	background-size:contain;
	background-repeat: no-repeat;
    animation: slide-left 2s forwards;
    -webkit-animation: slide-left 2s forwards;
    -moz-animation: slide-left 2s forwards;
    position: relative;
    animation-delay: 0s;
    height: 100px;
    width: 100px;
}

@-webkit-keyframes slide-left {
   	0%{
        left:0px;
    }
   	50%{
        left:200px;
    }
    100% {
        left:400px;
    }
}
@-moz-keyframes slide-left {
	0%{
        left:0px;
    }
    50%{
        left:200px;
    }
    100% {
        left:400px;
    }
}
@keyframes slide-left {
	0%{
        left:0px;
    }
    50%{
        left:200px;
    }
    100% {
        left:400px;
    }
}

.centerfix{
    margin:0px;
}

header{
    background-image: url("../images/scene-1(b).png");
}






